import React from 'react';
import { Link } from 'react-router-dom';

const NavigationBar = () => {
  return (
    <nav style={styles.navbar}>
      <ul style={styles.navList}>
      <li style={styles.navItem}><Link style={styles.navLink} to="/home">Home</Link></li>
        <li style={styles.navItem}><Link style={styles.navLink} to="/product-management">Product Management</Link></li>
        <li style={styles.navItem}><Link style={styles.navLink} to="/transaction-history">Transaction History</Link></li>
        <li style={styles.navItem}><Link style={styles.navLink} to="/user-management">User Management</Link></li>
        <li style={styles.navItem}><Link style={styles.navLink} to="/login">Logout</Link></li>
      </ul>
    </nav>
  );
};

const styles = {
  navbar: {
    backgroundColor: 'hotpink',
    padding: '10px',
    marginBottom: '20px',
    borderRadius: '5px',
  },
  navList: {
    listStyleType: 'none',
    display: 'flex',
    justifyContent: 'space-around',
  },
  navItem: {
    margin: '0 10px',
  },
  navLink: {
    textDecoration: 'none',
    color: 'white',
    fontSize: '16px',
    fontWeight: 'bold',
  },
};

export default NavigationBar;
