import React from 'react'; 
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const ImageCarousel = () => {
  const settings = {
    dots: true, // Show navigation dots
    infinite: true, // Enable infinite looping
    speed: 500, // Speed of transition
    slidesToShow: 1, // Display 1 image at a time
    slidesToScroll: 1, // Scroll 1 image at a time
    autoplay: true, // Enable automatic sliding
    autoplaySpeed: 7000, // Set the speed of the automatic sliding (3000 ms = 3 seconds)
  };

  return (
    <div>
      <Slider {...settings}>
        <div>
          <img src="https://t4.ftcdn.net/jpg/07/42/77/17/360_F_742771769_ju2WIp02qUz3ELShd4aEkHnwdlDDI71G.jpg" alt="Pink flowers" style={{width: "100%", height: "250px"}}/>
        </div>
        <div>
          <img src="https://png.pngtree.com/thumb_back/fh260/background/20220216/pngtree-pink-pattern-background-hd-picture-image_933027.jpg" alt="Levels" style={{width: "100%", height: "250px"}}/>
        </div>
        <div>
          <img src="https://t3.ftcdn.net/jpg/08/66/28/90/360_F_866289003_dye5lu5kAm4W3CpRqyvcLXYdqESRglIQ.jpg" alt="Delicious dessert" style={{width: "100%", height: "250px"}}/>
        </div>
        {/* Add more images as needed */}
      </Slider>
    </div>
  );
};

export default ImageCarousel;
