import React, { useState, useEffect } from 'react';

const ProductManagement = () => {
  const [products, setProducts] = useState([]);
  const [transactions, setTransactions] = useState([]);
  const [name, setName] = useState('');
  const [description, setDescription] = useState('');
  const [price, setPrice] = useState('');
  const [quantity, setQuantity] = useState('');
  const [category, setCategory] = useState('');

  const stockThreshold = 40; // Set threshold for low stock

  // Load stored products and transactions on component mount
  useEffect(() => {
    fetch('http://localhost:3001/api/products')  // Adjust the URL if needed

      .then((response) => response.json())
      .then((data) => setProducts(data))
      .catch((error) => console.error('Error fetching products:', error));

    const storedTransactions = JSON.parse(localStorage.getItem('transactions')) || [];
    setTransactions(storedTransactions);
  }, []);

  const addProduct = () => {
    const newProduct = { name, description, price, quantity: parseInt(quantity), category };
    fetch('http://localhost:3001/api/products', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(newProduct),
    })
      .then((response) => response.json())
      .then((product) => setProducts([...products, product]))
      .catch((error) => console.error('Error adding product:', error));

    setName('');
    setDescription('');
    setPrice('');
    setQuantity('');
    setCategory('');
  };

  const editProduct = (index) => {
    const product = products[index];
    setName(product.name);
    setDescription(product.description);
    setPrice(product.price);
    setQuantity(product.quantity);
    setCategory(product.category);
    deleteProduct(index);
  };

  const deleteProduct = (index) => {
    const product = products[index];
    fetch(`http://localhost:3001/api/products/${product.id}`, {
      method: 'DELETE',
    })
      .then(() => setProducts(products.filter((_, i) => i !== index)))
      .catch((error) => console.error('Error deleting product:', error));
  };

  const handleRestockProduct = (index) => {
    const product = products[index];
    const updatedProduct = { ...product, quantity: product.quantity + 1 };
    fetch(`http://localhost:3001/api/products/${product.id}`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(updatedProduct),
    })
      .then(() => {
        setProducts(products.map((prod, i) => (i === index ? updatedProduct : prod)));
        recordTransaction(product.name, 1, 'restock');
      })
      .catch((error) => console.error('Error restocking product:', error));
  };

  const handleSellProduct = (index) => {
    const product = products[index];
    if (product.quantity > 0) {
      const updatedProduct = { ...product, quantity: product.quantity - 1 };
      fetch(`http://localhost:3001/api/products/${product.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(updatedProduct),
      })
        .then(() => {
          setProducts(products.map((prod, i) => (i === index ? updatedProduct : prod)).filter((prod) => prod.quantity > 0));
          recordTransaction(product.name, 1, 'sell');
        })
        .catch((error) => console.error('Error selling product:', error));
    }
  };

  const recordTransaction = (productName, quantity, type) => {
    const newTransaction = {
      productName,
      quantity,
      type,
      date: new Date().toLocaleString()
    };
    const updatedTransactions = [...transactions, newTransaction];
    setTransactions(updatedTransactions);
    localStorage.setItem('transactions', JSON.stringify(updatedTransactions)); // Save to localStorage
  };

  const saveChanges = () => {
    localStorage.setItem('products', JSON.stringify(products));
    alert('Changes saved!');
  };

  const getStockStatus = (quantity) => {
    if (quantity <= stockThreshold) return 'Low Stock';
    if (quantity <= stockThreshold * 2) return 'Medium Stock';
    return 'In Stock';
  };

  const getStockStatusStyle = (status) => {
    if (status === 'Low Stock') return { color: 'red' };
    if (status === 'Medium Stock') return { color: 'orange' };
    return { color: 'green' };
  };

  return (
    <div style={styles.container}>
      <h1>Product Management</h1>

      <input type="text" placeholder="Product Name" value={name} onChange={(e) => setName(e.target.value)} style={styles.input} />
      <input type="text" placeholder="Description" value={description} onChange={(e) => setDescription(e.target.value)} style={styles.input} />
      <input type="number" placeholder="Price" value={price} onChange={(e) => setPrice(e.target.value)} style={styles.input} />
      <input type="number" placeholder="Quantity" value={quantity} onChange={(e) => setQuantity(e.target.value)} style={styles.input} />
      <input type="text" placeholder="Category" value={category} onChange={(e) => setCategory(e.target.value)} style={styles.input} />
      <button onClick={addProduct} style={styles.button}>Add Product</button>

      <table style={styles.table}>
        <thead>
          <tr>
            <th style={styles.tableHeader}>Name</th>
            <th style={styles.tableHeader}>Description</th>
            <th style={styles.tableHeader}>Price</th>
            <th style={styles.tableHeader}>Quantity</th>
            <th style={styles.tableHeader}>Category</th>
            <th style={styles.tableHeader}>Stock Status</th>
            <th style={styles.tableHeader}>Actions</th>
          </tr>
        </thead>
        <tbody>
          {products.map((product, index) => {
            const stockStatus = getStockStatus(product.quantity);
            const stockStyle = getStockStatusStyle(stockStatus);
            return (
              <tr key={index}>
                <td style={styles.tableCell}>{product.name}</td>
                <td style={styles.tableCell}>{product.description}</td>
                <td style={styles.tableCell}>{product.price}</td>
                <td style={styles.tableCell}>{product.quantity}</td>
                <td style={styles.tableCell}>{product.category}</td>
                <td style={{ ...styles.tableCell, ...stockStyle }}>{stockStatus}</td>
                <td style={styles.tableCell}>
                  <button onClick={() => editProduct(index)} style={styles.actionButton}>Edit</button>
                  <button onClick={() => deleteProduct(index)} style={styles.actionButton}>Delete</button>
                  <button onClick={() => handleRestockProduct(index)} style={styles.actionButton}>Restock</button>
                  <button onClick={() => handleSellProduct(index)} style={styles.actionButton}>Sell</button>
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>

      <button onClick={saveChanges} style={styles.saveButton}>Save Changes</button>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
  },
  input: {
    margin: '5px',
    padding: '8px',
    width: '200px',
  },
  button: {
    backgroundColor: 'hotpink',
    color: 'white',
    padding: '10px 20px',
    border: 'none',
    cursor: 'pointer',
    marginTop: '10px',
  },
  saveButton: {
    backgroundColor: 'green',
    color: 'white',
    padding: '10px 20px',
    border: 'none',
    cursor: 'pointer',
    marginTop: '20px',
  },
  table: {
    width: '100%',
    marginTop: '20px',
    borderCollapse: 'collapse',
  },
  tableHeader: {
    backgroundColor: '#f0f0f0',
    padding: '10px',
    textAlign: 'left',
    borderBottom: '2px solid #ddd',
  },
  tableCell: {
    padding: '10px',
    textAlign: 'left',
    borderBottom: '1px solid #ddd',
  },
  actionButton: {
    backgroundColor: 'hotpink',
    color: 'white',
    border: 'none',
    padding: '5px 10px',
    margin: '0 5px',
    cursor: 'pointer',
  },
  navbar: {
    display: 'flex',
    justifyContent: 'flex-start',
    backgroundColor: '#333',
    padding: '10px',
  },
  navItem: {
    color: 'white',
    textDecoration: 'none',
    padding: '10px 20px',
    margin: '0 5px',
    cursor: 'pointer',
  },
};

export default ProductManagement;
