import React, { useState, useEffect } from 'react';
import { Bar } from 'react-chartjs-2'; // Changed to Bar for graphing
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

// Register chart.js components
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const Dashboard = () => {
  const [products, setProducts] = useState([]);
  const STOCK_THRESHOLD = 40;

  useEffect(() => {
    const storedProducts = JSON.parse(localStorage.getItem('products')) || [];
    console.log('Products loaded from localStorage:', storedProducts);
    setProducts(storedProducts);
  }, []);

  // Function to determine stock status
  const getStockStatus = (quantity) => {
    if (quantity === 0) {
      return 'Out of Stock';
    } else if (quantity <= STOCK_THRESHOLD) {
      return 'Low Stock';
    } else {
      return 'In Stock';
    }
  };

  // Function to get image URL based on product name
  const getImageUrl = (productName) => {
    // Define a dictionary of product names and their corresponding image URLs
    const imageLinks = {
      'Rice': 'https://i.pinimg.com/736x/88/92/5a/88925add62d7154cd0b702e8ed015011.jpg',  // Replace with your actual image link
      'Bananas': 'https://www.bobtailfruit.co.uk/media/mageplaza/blog/post/4/2/42e9as7nataai4a6jcufwg.jpeg',     // Replace with your actual image link
      'Chips and chicken': 'https://i.pinimg.com/1200x/32/ff/90/32ff90f7f45e878a6fa64022f73c5b81.jpg',   // Replace with your actual image link
      'Niknaks': 'https://i.pinimg.com/1200x/4b/1f/d6/4b1fd690a1306e7212a1f97c6684ecc6.jpg',
      'Oranges': 'https://i.pinimg.com/236x/97/28/b7/9728b7799f2eeef9b8aab51d3ef0ead4.jpg',
      'Bic pens': 'https://i.pinimg.com/736x/59/8d/91/598d916cfc0c0b0da0b2fa836c56cfdd.jpg',
      'Pencils': 'https://i.pinimg.com/236x/80/f3/b5/80f3b577f6bacfcf77c2482cf42f4db1.jpg',
      'Markers': 'https://i.pinimg.com/736x/ce/7f/c3/ce7fc3f711b4d0ded7b51d7bb89fe41c.jpg',
      'Sellotape': 'https://i.pinimg.com/236x/cf/3c/72/cf3c727519cfd4329fe85a083bc36465.jpg',
      'Erasers': 'https://i.pinimg.com/736x/16/4c/19/164c193c6cddab19660fbcf881290b62.jpg',
      'Coke': 'https://i.pinimg.com/236x/bb/b4/25/bbb42583b411430e270f52b348032add.jpg',


      // Add more product names and their image URLs here
    };

    // Return the image link based on the product name or fallback to a placeholder image
    return imageLinks[productName] || `https://via.placeholder.com/150?text=${productName}`;
  };

  // Data for stock levels graph
  const chartData = {
    labels: products.map(product => product.name),
    datasets: [
      {
        label: 'Stock Levels',
        data: products.map(product => product.quantity),
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  };

  // Sort products by quantity in descending order
  const sortedProducts = [...products].sort((a, b) => b.quantity - a.quantity);

  return (
    <div style={styles.container}>
      <h1>Stock Dashboard</h1>
      <h2>Welcome to the Dashboard! You are successfully logged in.</h2>

      {/* Image Carousel */}
      <div style={styles.carouselContainer}>
        <Carousel
          additionalTransfrom={0}
          arrows
          autoPlay
          autoPlaySpeed={2000}
          centerMode={false}
          className=""
          containerClass="carousel-container"
          dotListClass=""
          draggable
          focusOnSelect={false}
          infinite
          itemClass=""
          keyBoardControl
          minimumTouchDrag={80}
          renderButtonGroupOutside={false}
          renderDotsOutside={false}
          responsive={{
            desktop: {
              breakpoint: { max: 3000, min: 1024 },
              items: 5,
              slidesToSlide: 3 // optional, default to 1.
            },
            tablet: {
              breakpoint: { max: 1024, min: 464 },
              items: 2,
              slidesToSlide: 2 // optional, default to 1.
            },
            mobile: {
              breakpoint: { max: 464, min: 0 },
              items: 1,
              slidesToSlide: 1 // optional, default to 1.
            }
          }}
          showDots={false}
          sliderClass=""
          slidesToSlide={1}
          swipeable
        >
          {products.map((product, index) => (
            <div key={index} style={styles.carouselImageContainer}>
              <img
                src={getImageUrl(product.name)}  // Get image URL based on the product name
                alt={product.name}
                style={styles.carouselImage}
              />
              <p>{product.name}</p>
            </div>
          ))}
        </Carousel>
      </div>

      {/* Stock Levels Graph */}
      <div style={styles.chartContainer}>
        <h2>Stock Levels Overview</h2>
        {products.length > 0 ? (
          <Bar data={chartData} />
        ) : (
          <p>No products available to display stock levels.</p>
        )}
      </div>

      {/* Product List with Stock Status */}
      <div style={styles.productList}>
        <h2>Product List</h2>
        <table style={styles.table}>
          <thead>
            <tr>
              <th style={styles.tableHeader}>Name</th>
              <th style={styles.tableHeader}>Quantity</th>
              <th style={styles.tableHeader}>Stock Status</th>
            </tr>
          </thead>
          <tbody>
            {sortedProducts.length > 0 ? (
              sortedProducts.map((product, index) => (
                <tr key={index}>
                  <td style={styles.tableCell}>{product.name}</td>
                  <td style={styles.tableCell}>{product.quantity}</td>
                  <td style={styles.tableCell}>
                    <span
                      style={{
                        ...styles.stockStatus,
                        backgroundColor:
                          product.quantity === 0
                            ? '#FF6347'
                            : product.quantity <= STOCK_THRESHOLD
                            ? '#FFA500'
                            : '#32CD32',
                      }}
                    >
                      {getStockStatus(product.quantity)}
                    </span>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="3" style={styles.tableCell}>
                  No products available.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
  },
  carouselContainer: {
    marginBottom: '20px',
    padding: '20px',
    backgroundColor: '#f7f7f7',
    borderRadius: '8px',
  },
  carouselImageContainer: {
    textAlign: 'center',
    padding: '10px',
  },
  carouselImage: {
    width: '100%',
    height: 'auto',
    borderRadius: '5px',
  },
  chartContainer: {
    marginBottom: '20px',
  },
  productList: {
    marginTop: '40px',
  },
  table: {
    width: '100%',
    marginTop: '20px',
    borderCollapse: 'collapse',
  },
  tableHeader: {
    backgroundColor: '#f0f0f0',
    padding: '10px',
    textAlign: 'left',
    borderBottom: '2px solid #ddd',
  },
  tableCell: {
    padding: '10px',
    textAlign: 'left',
    borderBottom: '1px solid #ddd',
  },
  stockStatus: {
    padding: '5px 10px',
    borderRadius: '5px',
    color: 'white',
  },
};

export default Dashboard;
