// UserManagement.js
import React, { useEffect, useState } from 'react';

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [isEditing, setIsEditing] = useState(false);
  const [editedUser, setEditedUser] = useState({ name: '', email: '' });
  const [editingIndex, setEditingIndex] = useState(null);

  useEffect(() => {
    const savedUsers = JSON.parse(localStorage.getItem('users')) || [];
    setUsers(savedUsers);
  }, []);

  const deleteUser = (index) => {
    const updatedUsers = users.filter((_, i) => i !== index);
    setUsers(updatedUsers);
    localStorage.setItem('users', JSON.stringify(updatedUsers));
  };

  const editUser = (index) => {
    setEditingIndex(index);
    setEditedUser({ name: users[index].name, email: users[index].email });
    setIsEditing(true);
  };

  const saveEditedUser = () => {
    const updatedUsers = [...users];
    updatedUsers[editingIndex] = editedUser;
    setUsers(updatedUsers);
    localStorage.setItem('users', JSON.stringify(updatedUsers));
    setIsEditing(false);
    setEditedUser({ name: '', email: '' });
    setEditingIndex(null);
  };

  return (
    <div style={styles.container}>
      <h1>User Management</h1>
      <table style={styles.table}>
        <thead>
          <tr>
            <th style={styles.tableHeader}>User Name</th>
            <th style={styles.tableHeader}>Email</th>
            <th style={styles.tableHeader}>Actions</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={index}>
              <td style={styles.tableCell}>
                {isEditing && editingIndex === index ? (
                  <input
                    type="text"
                    value={editedUser.name}
                    onChange={(e) => setEditedUser({ ...editedUser, name: e.target.value })}
                    style={styles.editInput}
                  />
                ) : (
                  user.name
                )}
              </td>
              <td style={styles.tableCell}>
                {isEditing && editingIndex === index ? (
                  <input
                    type="email"
                    value={editedUser.email}
                    onChange={(e) => setEditedUser({ ...editedUser, email: e.target.value })}
                    style={styles.editInput}
                  />
                ) : (
                  user.email
                )}
              </td>
              <td style={styles.tableCell}>
                {isEditing && editingIndex === index ? (
                  <button onClick={saveEditedUser} style={styles.actionButton}>Save</button>
                ) : (
                  <>
                    <button onClick={() => editUser(index)} style={styles.actionButton}>Edit</button>
                    <button onClick={() => deleteUser(index)} style={styles.actionButton}>Delete</button>
                  </>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

const styles = {
  container: {
    padding: '20px',
  },
  table: {
    width: '100%',
    marginTop: '20px',
    borderCollapse: 'collapse',
  },
  tableHeader: {
    backgroundColor: '#f0f0f0',
    padding: '10px',
    textAlign: 'left',
    borderBottom: '2px solid #ddd',
  },
  tableCell: {
    padding: '10px',
    textAlign: 'left',
    borderBottom: '1px solid #ddd',
  },
  actionButton: {
    backgroundColor: 'hotpink',
    color: 'white',
    border: 'none',
    padding: '5px 10px',
    cursor: 'pointer',
    marginRight: '10px',
  },
  editInput: {
    padding: '5px',
    width: '150px',
  },
};

export default UserManagement;
