import React, { useEffect, useState } from "react";

function TransactionHistory() {
  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    // Retrieve the transaction history from local storage
    const storedTransactions = JSON.parse(localStorage.getItem("transactions")) || [];
    setTransactions(storedTransactions);
  }, []);

  const handleDeleteAll = () => {
    // Remove all transactions from local storage and update state
    localStorage.removeItem("transactions");
    setTransactions([]);
  };

  return (
    <div>
      <h1>Transaction History</h1>
      <p>Here is the list of all transactions.</p>

      <table style={styles.table}>
        <thead>
          <tr>
            <th style={styles.tableHeader}>Product</th>
            <th style={styles.tableHeader}>Quantity</th>
            <th style={styles.tableHeader}>Type</th>
            <th style={styles.tableHeader}>Date</th>
          </tr>
        </thead>
        <tbody>
          {transactions.map((transaction, index) => (
            <tr key={index}>
              <td style={styles.tableCell}>{transaction.productName}</td>
              <td style={styles.tableCell}>{transaction.quantity}</td>
              <td style={styles.tableCell}>{transaction.type}</td>
              <td style={styles.tableCell}>{transaction.date}</td>
            </tr>
          ))}
        </tbody>
      </table>

      <button onClick={handleDeleteAll} style={styles.deleteButton}>
        Delete All History
      </button>
    </div>
  );
}

const styles = {
  table: {
    width: '100%',
    marginTop: '20px',
    borderCollapse: 'collapse',
  },
  tableHeader: {
    backgroundColor: '#f0f0f0',
    padding: '10px',
    textAlign: 'left',
    borderBottom: '2px solid #ddd',
  },
  tableCell: {
    padding: '10px',
    textAlign: 'left',
    borderBottom: '1px solid #ddd',
  },
  deleteButton: {
    marginTop: '20px',
    padding: '10px 20px',
    backgroundColor: '#ff4d4d',
    color: 'white',
    border: 'none',
    borderRadius: '5px',
    cursor: 'pointer',
  },
};

export default TransactionHistory;
